		flows: [{ src: 'https://file02.xgimi.com/bbs/twoexploit/thread/CR-gesRigu2RD.png', title: '极米 X 生活家装饰 | 您有一份价值1111的红包待领取！', text: '这一次，品质生活提供商生活家装饰联合无屏电视开创者极米科技，从居家物质追求到精神角落充填一并出发，共同探寻品质生活家的真实模样。' }, { src: 'https://file02.xgimi.com/bbs/twoexploit/thread/CR-bH8gqaOlig.jpg', title: '极米双十一四连冠 但我并没有那么高兴', text: '2017年双十一结束了，先简单给大家汇报下成绩吧。和往常一样，没有太大悬念，极米连续四年获得第一，双十一当天全网销售总额突破2亿元。在京东平台，极米无屏电视双十一销售额是第二名的五倍。在天猫平台，11日20点11分，天猫单店销售额率先突破1亿元，并跻身天猫双十一“亿元俱乐部”' }, { src: 'https://file02.xgimi.com/bbs/twoexploit/thread/CR-6j37RkBFyy.jpg', title: '极米A1 Pro和CC极光斩获CES 2018创新大奖', text: '美东时间11月9日下午（北京时间11月10日凌晨），全球消费类电子第一展CES在纽约正式揭晓2018年度创新奖项，极米激光无屏电视A1 Pro和极米无屏电视CC极光双双获得创新大奖。其中，A1 Pro获得2018年CES家用影音设备和配件品类创新大奖，CC极光获得2018年CES便携式播放器和配件品类创新大奖。' }, { src: 'https://file02.xgimi.com/bbs/twoexploit/thread/CR-hZu15XmZOP.jpg', title: '双十一宝妈购物推荐：imea儿童智能投影', text: '之前上班的时候，公婆特别宠大宝，天天给她看电视，结果去医院一查发现弱视了，找了好久找到这款智能投影，这款是红外息屏，蓝光调校的，看的时候很大程度减少了对视力的伤害，里边的内容全是筛选过的，不用怕大宝乱按按到不良信息。重点是！可以远程操控，今年暑假真的是全靠这台了，大宝每天看电视的时间我上班都能控制了。' }, { src: 'https://file02.xgimi.com/bbs/twoexploit/thread/CR-Kyr920oBSH.jpg', title: '[极客试界] 第78期 幻响风小净电动智能防霾口罩 0元试用', text: '秋冬季最怕的便是雾霾、灰尘了，为了健康，口罩是必须配备的。随着科技进步，口罩逐渐先进起来。市面上的智能口罩层出不穷、好坏参差不齐。到底应该如何选购智能防霾口罩才不会被坑？真智能还是真忽悠？欢迎报名体验本期免费试用产品——幻响风小净电动智能防霾口罩，一试便知！' }, { src: 'https://file02.xgimi.com/bbs/twoexploit/thread/CR-uOArbhOJcc.jpg', title: '极米双11 极米商城全网价保 免单 配件 VIP卡 都送！', text: '亲爱的极友，我们定了一个小目标：继续蝉联双11全网销量冠军！为了完成这个小目标，我们需要你参与一下，比如说——极米双十一，买几台极米来自己用、送朋友、送老师、送老板、送媳妇儿啥的，这都是可以的！大家加把劲儿，保送极米成销冠！' }, { src: 'https://file02.xgimi.com/bbs/twoexploit/thread/CR-4otklIPQ5q.png', title: '[社区活动]助力双十一，踩楼赢奖励', text: '这个世界对单身狗已经越来越没有包容之心了，好好的一个光棍节愣是被玩成了一年一度的狂欢购物节。也对，除了买买买好像也没有其它方式能让单身狗们得到相对较多的快乐。今年双十一，你准备剁手了没？？？' }, { src: 'https://file02.xgimi.com/bbs/twoexploit/thread/CR-oSdiMG22b9.png', title: '画质好 音质佳 更有人情味——杭州极米直营店体验', text: '新店刚刚安家落户，店员小哥不停的整理货柜忙的满头汗水。可是当小朋友进店体验imea时，他马上放下手里的事情，耐心的在一旁陪伴引导。一款智能产品走进用户的心里，并不需要花哨的噱头或者华丽的数据，这种面对面的感受最直接也最能打动用户。' }, { src: 'https://file02.xgimi.com/bbs/twoexploit/thread/CR-31fGcDCDLb.jpg', title: '激光无屏电视A1Pro、imea儿童机线下品鉴会 欢迎报名！', text: '为方便大家更加详细地了解极米激光无屏电视与imea儿童投影灯极米产品，我们决定在全国8座城市9个门店同时启动极米激光电视A1Pro与imea儿童无屏电视品鉴会，活动将于2017年11月4日星期六下午14：30开始签到，15时正式开始活动。报名审核通过的极友，请准时前往所在城市的极米旗舰店签到赴约，即可现场体验激光无屏电视A1Pro、imea儿童智能投影等极米产品。' }, { src: 'https://file02.xgimi.com/bbs/twoexploit/thread/CR-H0tKbctQMQ.jpg', title: '大家好，给大家介绍一下，这是极米的GMUI3.0', text: 'Z5系列机型已率先搭载GMUI3.0，其他兄弟机型也陆续加入3.0大军。目前H1、H1极光也正式推送3.0，H1S和H1S极光机型的GMUI3.0公测也已完成，正式版固件即将和大家见面，敬请期待。我们正按计划在准备Z4X机型的公测固件，请持续关注我们。这回简单制作了一期视频介绍，方便大家更清晰全面直观的认识新UI，极米这新UI这段路，还得靠大伙儿多担待接纳它，我们一起齐头并进，继续向前跑吧！' }],
